@charset "UTF-8"; /***** Base *****/
.announcementScreen * {
    box-sizing: border-box;
}

.announcementScreen body {
    background-color: rgba(241, 243, 246, 0);
    color: #333333;
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif;
    font-size: 15px;
    font-weight: 400;
    line-height: 1.5;
    -webkit-font-smoothing: antialiased;
}

@media (min-width: 1024px) {
    .announcementScreen body > main {
        min-height: 65vh;
    }
}

.announcementScreen h1, h2, h3, h4, h5, h6 {
    font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif;
    font-weight: bold;
    margin-top: 0;
}

.announcementScreen h1 {
    font-size: 32px;
}

.announcementScreen h2 {
    font-size: 22px;
}

.announcementScreen h3 {
    font-size: 18px;
    font-weight: 600;
}

.announcementScreen h4 {
    font-size: 16px;
}

.announcementScreen a {
    color: rgba(24, 166, 137, 1);
    text-decoration: none;
}

.announcementScreen a:hover, a:active, a:focus {
    text-decoration: underline;
}

.announcementScreen input, textarea {
    color: #000;
    font-size: 14px;
}

.announcementScreen input {
    font-weight: 300;
    max-width: 100%;
    box-sizing: border-box;
    outline: none;
    transition: border .12s ease-in-out;
}

.announcementScreen input:focus {
    border: 1px solid rgba(27, 179, 148, 1);
}

.announcementScreen input[disabled] {
    background-color: #ddd;
}

.announcementScreen select {
    -webkit-appearance: none;
    -moz-appearance: none;
    background: url(//theme.zdassets.com/theme_assets/2109657/27f536d448438fdd88b4004a3223bcc153fb621d.svg) no-repeat #fff;
    background-position: right 10px center;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 8px 30px 8px 10px;
    outline: none;
    color: #555;
    width: 100%;
}

.announcementScreen select:focus {
    border: 1px solid rgba(27, 179, 148, 1);
}

.announcementScreen select::-ms-expand {
    display: none;
}

.announcementScreen textarea {
    border: 1px solid #ddd;
    border-radius: 2px;
    resize: vertical;
    width: 100%;
    outline: none;
    padding: 10px;
}

.announcementScreen textarea:focus {
    border: 1px solid rgba(27, 179, 148, 1);
}

.announcementScreen .container {
    max-width: 1160px;
    margin: 0 auto;
    padding: 0 5%;
}

@media (min-width: 1160px) {
    .announcementScreen .container {
        padding: 0;
        width: 90%;
    }
}

.announcementScreen .container-divider {
    border-top: 1px solid #ddd;
    margin-bottom: 20px;
}

.announcementScreen ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.announcementScreen .error-page {
    max-width: 1160px;
    margin: 0 auto;
    padding: 0 5%;
}

@media (min-width: 1160px) {
    .announcementScreen .error-page {
        padding: 0;
        width: 90%;
    }
}

.announcementScreen .dropdown-toggle::after {
    color: inherit;
}

/***** Buttons *****/
.announcementScreen .button, .announcementScreen [role="button"] {
    border: 1px solid rgba(27, 179, 148, 1);
    border-radius: 4px;
    color: rgba(27, 179, 148, 1);
    cursor: pointer;
    display: inline-block;
    font-size: 12px;
    line-height: 2.34;
    margin: 0;
    padding: 0 20px;
    text-align: center;
    transition: background-color .12s ease-in-out, border-color .12s ease-in-out, color .15s ease-in-out;
    user-select: none;
    white-space: nowrap;
    width: 100%;
    -webkit-touch-callout: none;
}

@media (min-width: 768px) {
    .announcementScreen .button, [role="button"] {
        width: auto;
    }
}

.announcementScreen .button::after, [role="button"]::after {
    color: rgba(27, 179, 148, 1);
}

.announcementScreen .button:hover, .announcementScreen .button:active, .announcementScreen .button:focus,.announcementScreen .button[aria-selected="true"],.announcementScreen [role="button"]:hover,.announcementScreen [role="button"]:active,.announcementScreen [role="button"]:focus,.announcementScreen [role="button"][aria-selected="true"] {
    background-color: rgba(27, 179, 148, 1);
    color: #ffffff;
    text-decoration: none;
}

.announcementScreen .button[aria-selected="true"]:hover, .button[aria-selected="true"]:focus, .button[aria-selected="true"]:active, [role="button"][aria-selected="true"]:hover, [role="button"][aria-selected="true"]:focus, [role="button"][aria-selected="true"]:active {
    background-color: #0e5a4b;
    border-color: #0e5a4b;
}

.announcementScreen .button[data-disabled], [role="button"][data-disabled] {
    cursor: default;
}

.announcementScreen .button-large, input[type="submit"] {
    background-color: rgba(27, 179, 148, 1);
    border: 0;
    border-radius: 4px;
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    line-height: 2.72;
    min-width: 190px;
    padding: 0 1.9286em;
    width: 100%;
}

@media (min-width: 768px) {
    .announcementScreen .button-large, input[type="submit"] {
        width: auto;
    }
}

.announcementScreen .button-large:hover, .button-large:active, .button-large:focus, input[type="submit"]:hover, input[type="submit"]:active, input[type="submit"]:focus {
    background-color: #0e5a4b;
}

.announcementScreen .button-large[disabled], input[type="submit"][disabled] {
    background-color: #ddd;
}

.announcementScreen .button-secondary {
    color: #666;
    border: 1px solid #ddd;
    background-color: transparent;
}

.announcementScreen .button-secondary:hover, .button-secondary:focus, .button-secondary:active {
    color: #333333;
    border: 1px solid #ddd;
    background-color: rgba(232,235,240,0);
}

/***** Tables *****/
.announcementScreen .table {
    width: 100%;
    table-layout: fixed;
}

@media (min-width: 768px) {
    .announcementScreen .table {
        table-layout: auto;
    }
}

.announcementScreen .table th, .table th a {
    color: #666;
    font-size: 13px;
    font-weight: 300;
    text-align: left;
}

.announcementScreen [dir="rtl"] .table th, [dir="rtl"] .table th a {
    text-align: right;
}

.announcementScreen .table tr {
    border-bottom: 1px solid #ddd;
    display: block;
    padding: 20px 0;
}

@media (min-width: 768px) {
    .announcementScreen .table tr {
        display: table-row;
    }
}

.announcementScreen .table td {
    display: block;
}

@media (min-width: 768px) {
    .announcementScreen .table td {
        display: table-cell;
    }
}

@media (min-width: 1024px) {
    .announcementScreen .table td, .table th {
        padding: 20px 30px;
    }
}

@media (min-width: 768px) {
    .announcementScreen .table td, .table th {
        padding: 10px 20px;
        height: 60px;
    }
}

/***** Forms *****/
.announcementScreen .form {
    max-width: 650px;
}

.announcementScreen .form-field ~ .form-field {
    margin-top: 25px;
}

.announcementScreen .form-field label {
    display: block;
    font-size: 13px;
    margin-bottom: 5px;
}

.announcementScreen .form-field input {
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 10px;
    width: 100%;
}

.announcementScreen .form-field input:focus {
    border: 1px solid rgba(27, 179, 148, 1);
}

.announcementScreen .form-field input[type="text"] {
    border: 1px solid #ddd;
    border-radius: 4px;
}

.announcementScreen .form-field input[type="text"]:focus {
    border: 1px solid rgba(27, 179, 148, 1);
}

.announcementScreen .form-field input[type="checkbox"] {
    width: auto;
}

.announcementScreen .form-field .nesty-input {
    border-radius: 4px;
    height: 40px;
    line-height: 40px;
    outline: none;
    vertical-align: middle;
}

.announcementScreen .form-field .nesty-input:focus {
    border: 1px solid rgba(27, 179, 148, 1);
    text-decoration: none;
}

.announcementScreen .form-field textarea {
    vertical-align: middle;
}

.announcementScreen .form-field input[type="checkbox"] + label {
    margin: 0 0 0 10px;
}

.announcementScreen .form-field.required > label::after {
    content: "*";
    color: #f00;
    margin-left: 2px;
}

.announcementScreen .form-field p {
    color: #666;
    font-size: 12px;
    margin: 5px 0;
}

.announcementScreen [data-loading="true"] input, [data-loading="true"] textarea {
    background: transparent url(//theme.zdassets.com/theme_assets/2109657/6aae8ce36967837f706ac36deff4b753e43977a4.gif) 99% 50% no-repeat;
    background-size: 16px 16px;
}

.announcementScreen .form footer {
    margin-top: 40px;
    padding-top: 30px;
}

.announcementScreen .form footer a {
    color: #666;
    cursor: pointer;
    margin-right: 15px;
}

.announcementScreen .form .suggestion-list {
    font-size: 13px;
    margin-top: 30px;
}

.announcementScreen .form .suggestion-list label {
    border-bottom: 1px solid #ddd;
    display: block;
    padding-bottom: 5px;
}

.announcementScreen .form .suggestion-list li {
    padding: 10px 0;
}

.announcementScreen .form .suggestion-list li a:visited {
    color: #0b4d3f;
}

/***** Header *****/
.announcementScreen .header {
    max-width: 1160px;
    margin: 0 auto;
    padding: 0 5%;
    position: relative;
    align-items: center;
    display: flex;
    height: 71px;
    justify-content: space-between;
}

@media (min-width: 1160px) {
    .announcementScreen .header {
        padding: 0;
        width: 90%;
    }
}

.announcementScreen .logo img {
    max-height: 37px;
}

.announcementScreen .user-nav {
    display: inline-block;
    position: absolute;
    white-space: nowrap;
}

@media (min-width: 768px) {
    .announcementScreen .user-nav {
        position: relative;
    }
}

.announcementScreen .user-nav[aria-expanded="true"] {
    background-color: #fff;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.15), 0 4px 10px 0 rgba(0, 0, 0, 0.1);
    border: solid 1px #ddd;
    right: 0;
    left: 0;
    top: 71px;
    z-index: 1;
}

.announcementScreen .user-nav[aria-expanded="true"] > a {
    display: block;
    margin: 20px;
}

.announcementScreen .nav-wrapper a {
    border: 0;
    color: rgba(24, 166, 137, 1);
    display: none;
    font-size: 14px;
    padding: 0 20px 0 0;
    width: auto;
}

@media (min-width: 768px) {
    .announcementScreen .nav-wrapper a {
        display: inline-block;
    }
}

.announcementScreen [dir="rtl"] .nav-wrapper a {
    padding: 0 0 0 20px;
}

.announcementScreen .nav-wrapper a:hover, .nav-wrapper a:focus, .nav-wrapper a:active {
    background-color: transparent;
    color: rgba(24, 166, 137, 1);
    text-decoration: underline;
}

.announcementScreen .nav-wrapper a.login {
    display: inline-block;
}

.announcementScreen .nav-wrapper .icon-menu {
    display: inline-block;
    margin-right: 10px;
    color: rgba(24, 166, 137, 1);
}

@media (min-width: 768px) {
    .announcementScreen .nav-wrapper .icon-menu {
        display: none;
    }
}

.announcementScreen [dir="rtl"] .nav-wrapper .icon-menu {
    margin-left: 10px;
    margin-right: 0;
}

/***** User info in header *****/
.announcementScreen .user-info {
    display: inline-block;
}

.announcementScreen .user-info .dropdown-toggle::after {
    display: none;
}

@media (min-width: 768px) {
    .announcementScreen .user-info .dropdown-toggle::after {
        display: inline-block;
    }
}

.announcementScreen .user-info > [role="button"] {
    border: 0;
    color: rgba(24, 166, 137, 1);
    min-width: 0;
    padding: 0;
    white-space: nowrap;
}

.announcementScreen .user-info > [role="button"]:hover {
    color: rgba(24, 166, 137, 1);
    background-color: transparent;
}

.announcementScreen .user-info > [role="button"]::after {
    color: rgba(24, 166, 137, 1);
    padding-right: 15px;
}

.announcementScreen [dir="rtl"] .user-info > [role="button"]::after {
    padding-left: 15px;
    padding-right: 0;
}

.announcementScreen #user #user-name {
    display: none;
    font-size: 14px;
}

@media (min-width: 768px) {
    .announcementScreen #user #user-name {
        display: inline-block;
    }
}

.announcementScreen #user #user-name:hover {
    text-decoration: underline;
}

/***** User avatar *****/
.announcementScreen .user-avatar {
    height: 25px;
    width: 25px;
    border-radius: 50%;
    display: inline-block;
    vertical-align: middle;
}

.announcementScreen .avatar {
    display: inline-block;
    position: relative;
}

.announcementScreen .avatar img {
    height: 40px;
    width: 40px;
}

.announcementScreen .avatar .icon-agent::before {
    background-color: rgba(27, 179, 148, 1);
    border: 2px solid #fff;
    border-radius: 50%;
    bottom: -4px;
    color: #ffffff;
    content: "\1F464";
    font-size: 14px;
    height: 14px;
    line-height: 14px;
    position: absolute;
    right: -2px;
    text-align: center;
    width: 14px;
}

/***** Footer *****/
.announcementScreen .footer {
    border-top: 1px solid #ddd;
    margin-top: 60px;
    padding: 30px 0;
}

.announcementScreen .footer a {
    color: #666;
}

.announcementScreen .footer-inner {
    max-width: 1160px;
    margin: 0 auto;
    padding: 0 5%;
    display: flex;
    justify-content: space-between;
}

@media (min-width: 1160px) {
    .announcementScreen .footer-inner {
        padding: 0;
        width: 90%;
    }
}

.announcementScreen .footer-language-selector {
    color: #666;
    display: inline-block;
    font-weight: 300;
}

/***** Breadcrumbs *****/
.announcementScreen .breadcrumbs {
    margin: 0 0 15px 0;
    padding: 0;
}

@media (min-width: 768px) {
    .announcementScreen .breadcrumbs {
        margin: 0;
    }
}

.announcementScreen .breadcrumbs li {
    color: #666;
    display: inline;
    font-weight: 300;
    font-size: 13px;
    max-width: 450px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.announcementScreen .breadcrumbs li + li::before {
    content: ">";
    margin: 0 4px;
}

.announcementScreen .breadcrumbs li a:visited {
    color: rgba(24, 166, 137, 1);
}

/***** Search field *****/
.announcementScreen .search {
    position: relative;
}

.announcementScreen .search input[type="search"] {
    border: 1px solid #ddd;
    border-radius: 30px;
    box-sizing: border-box;
    color: #999;
    height: 40px;
    padding-left: 40px;
    padding-right: 20px;
    -webkit-appearance: none;
    width: 100%;
}

.announcementScreen [dir="rtl"] .search input[type="search"] {
    padding-right: 40px;
    padding-left: 20px;
}

.announcementScreen .search input[type="search"]:focus {
    border: 1px solid rgba(27, 179, 148, 1);
    color: #555;
}

.announcementScreen .search::before {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    background-color: #fff;
    color: #ddd;
    content: "\1F50D";
    font-size: 18px;
    position: absolute;
    left: 15px;
}

.announcementScreen [dir="rtl"] .search::before {
    right: 15px;
    left: auto;
}

.announcementScreen .search-full input[type="search"] {
    border: 1px solid #fff;
}

/***** Hero component *****/
.announcementScreen .hero {
    background-image: url(//theme.zdassets.com/theme_assets/2109657/2256cfa9f0eac7893302a37fb5bfca9b813ac5d0.png);
    background-position: center;
    background-size: cover;
    height: 300px;
    padding: 0 20px;
    text-align: center;
    width: 100%;
}

.announcementScreen .hero-inner {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    max-width: 610px;
    margin: 0 auto;
}

.announcementScreen .page-header {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 10px 0;
}

@media (min-width: 768px) {
    .announcementScreen .page-header {
        align-items: baseline;
        flex-direction: row;
        margin: 0;
    }
}

.announcementScreen .page-header .section-subscribe {
    flex-shrink: 0;
    margin-bottom: 10px;
}

@media (min-width: 768px) {
    .announcementScreen .page-header .section-subscribe {
        margin-bottom: 0;
    }
}

.announcementScreen .page-header h1 {
    flex-grow: 1;
    margin-bottom: 10px;
}

.announcementScreen .page-header-description {
    font-style: italic;
    font-weight: 300;
    margin: 0 0 30px 0;
    word-break: break-word;
}

@media (min-width: 1024px) {
    .announcementScreen .page-header-description {
        flex-basis: 100%;
    }
}

.announcementScreen .page-header .icon-lock::before {
    content: "\1F512";
    font-size: 20px;
    position: relative;
    left: -5px;
    vertical-align: baseline;
}

.announcementScreen .sub-nav {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin-bottom: 30px;
    min-height: 50px;
    padding-bottom: 15px;
}

@media (min-width: 768px) {
    .announcementScreen .sub-nav {
        align-items: baseline;
        flex-direction: row;
    }
}

@media (min-width: 768px) {
    .announcementScreen .sub-nav input[type="search"] {
        min-width: 300px;
    }
}

.announcementScreen .sub-nav input[type="search"]::after {
    font-size: 15px;
}

/***** Blocks *****/
/* Used in Homepage#categories and Community#topics */
.announcementScreen .blocks-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    list-style: none;
    padding: 0;
}

@media (min-width: 768px) {
    .announcementScreen .blocks-list {
        margin: 0 -15px;
    }
}

.announcementScreen .blocks-item {
    border: 1px solid rgba(27, 179, 148, 1);
    border-radius: 4px;
    box-sizing: border-box;
    color: rgba(27, 179, 148, 1);
    display: flex;
    flex: 1 0 340px;
    flex-direction: column;
    justify-content: center;
    margin: 0 0 30px;
    max-width: 100%;
    text-align: center;
}

@media (min-width: 768px) {
    .announcementScreen .blocks-item {
        margin: 0 15px 30px;
    }
}

.announcementScreen .blocks-item:hover, .blocks-item:focus, .blocks-item:active {
    background-color: rgba(27, 179, 148, 1);
}

.announcementScreen .blocks-item:hover *, .blocks-item:focus *, .blocks-item:active * {
    color: #ffffff;
    text-decoration: none;
}

.announcementScreen .blocks-item-internal {
    background-color: transparent;
    border: 1px solid #ddd;
}

.announcementScreen .blocks-item-internal .icon-lock::before {
    content: "\1F512";
    font-size: 15px;
    bottom: 5px;
    position: relative;
}

.announcementScreen .blocks-item-internal a {
    color: #333333;
}

.announcementScreen .blocks-item-link {
    color: rgba(27, 179, 148, 1);
    padding: 20px 30px;
}

.announcementScreen .blocks-item-link:hover, .blocks-item-link:focus, .blocks-item-link:active {
    text-decoration: none;
}

.announcementScreen .blocks-item-title {
    margin-bottom: 0;
}

.announcementScreen .blocks-item-description {
    font-weight: 300;
    margin: 0;
}

.announcementScreen .blocks-item-description:not(:empty) {
    margin-top: 10px;
}

/***** Homepage *****/
.announcementScreen .section {
    margin-bottom: 40px;
}

@media (min-width: 768px) {
    .announcementScreen .section {
        margin-bottom: 60px;
    }
}

.announcementScreen .section h2 {
    margin-bottom: 10px;
    text-align: center;
}

/***** Promoted articles *****/
.announcementScreen .promoted-articles {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
}

@media (min-width: 1024px) {
    .announcementScreen .promoted-articles {
        flex-direction: row;
    }
}

.announcementScreen .promoted-articles-item {
    flex: 1 0 auto;
}

@media (min-width: 1024px) {
    .announcementScreen .promoted-articles-item {
        align-self: flex-end;
        flex: 0 0 auto;
        padding-right: 30px;
        width: 33%;
        /* Three columns on desktop */
    }

    .announcementScreen [dir="rtl"] .promoted-articles-item {
        padding: 0 0 0 30px;
    }
}

.announcementScreen .promoted-articles-item:nth-child(3n) {
    padding-right: 0;
}

.announcementScreen .promoted-articles-item a {
    border-bottom: 1px solid #ddd;
    color: #333333;
    display: block;
    padding: 15px 0;
}

.announcementScreen .promoted-articles-item:last-child a {
    border: 0;
}

@media (min-width: 1024px) {
    .announcementScreen .promoted-articles-item:last-child a {
        border-bottom: 1px solid #ddd;
    }
}

/***** Community section in homepage *****/
.announcementScreen .community {
    text-align: center;
}

.announcementScreen .community-image {
    min-height: 300px;
    background-image: url(//theme.zdassets.com/theme_assets/2109657/9d05c36866877ead2570b9953339556e23de7ee3.jpg);
    background-position: center;
    background-repeat: no-repeat;
    max-width: 100%;
}

.announcementScreen .community, .activity {
    border-top: 1px solid #ddd;
    padding: 30px 0;
}

/***** Recent activity *****/
.announcementScreen .recent-activity-header {
    margin-bottom: 10px;
    text-align: center;
}

.announcementScreen .recent-activity-list {
    padding: 0;
}

.announcementScreen .recent-activity-item {
    border-bottom: 1px solid #ddd;
    overflow: auto;
    padding: 20px 0;
}

.announcementScreen .recent-activity-item-parent {
    font-size: 16px;
}

